<template>
  <p>Reaction Time : {{ score }} ms</p>
  <p>Rank : {{ rank }}</p>
</template>

<script>
export default {
    props: ['score'],
    data(){
        return {
            rank: null
        }
    
    },
    mounted(){
        if (this.score < 250) {
            this.rank = 'Ninja Finger'
        }
        else if (this.score < 400) {
            this.rank = 'mayan lahh'
        }
        else if (this.score < 1000) {
            this.rank = "slow hand blok"
        }
    }
}
</script>

